.skillsBlock {
    min-height: 100vh;
    --tw-bg-opacity: 1;
    background-color: rgb(22 22 22 / var(--tw-bg-opacity));
}
* {}
.title {
    height: 200px;
    width: 350px;
    font-size: 50px;
    font-weight: 600;
    color: #2B5B9DFF;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
}

.skills {
    width: 89%;
    margin:auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-right: 2%;

}

/* iPhone 12, 12 Pro, 13, 13 Pro (портретный режим) */
@media  screen and (orientation: portrait) and (min-device-width: 390px) and (max-device-height: 844px) {
    .skillsBlock {
        display: flex;
        flex-wrap: wrap;
        margin: auto;
    }

    .title {
        height: 0;
        width: 300px;
        margin-bottom: 50px;
    }

    .skills {
        margin:auto;
        width: 100%;
        }
}

/* iPhone 12, 12 Pro, 13, 13 Pro (альбомный режим) */
@media only screen and (orientation: landscape) and (min-device-width: 390px) and (max-device-height: 844px){
    .title {
        height: 100px;
    }

     .skills {
         display: flex;
         justify-content: space-evenly;
         width: 90%;
         margin-right: 1%;
    }

}
